import React from 'react';

import SEO from 'components/common/seo';
import Layout from './layout';
import Header from './Header';
import Footer from './Footer';
import SideNav from './SideNav';

import 'assets/sass/custom.bootstrap.scss';
import 'assets/sass/app.scss';

const Root = ({ children }) => {
  const childrenToLayoutMapRender =
    children.length > 1 ? (
      children.map(child => <Layout>{child}</Layout>)
    ) : (
      <Layout>{children}</Layout>
    );

  return (
    <div className="wrapper container-fluid pt-0 d-block d-md-flex flex-column min-vh-100">
      <SEO />
      <Header />
      <SideNav />
      <>{childrenToLayoutMapRender}</>
      <Footer />
    </div>
  );
};

export default Root;
